@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
.wrapper {
    width: 340px;
    height: 540px;
    padding: 40px;
    border-radius: 25px;
    background-color: #494455;
  }
.screen {
    height: 100px;
    width: 100%;
    margin-bottom: 20px;
    padding: 0 10px;
    background-color: #98a194;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: black;
    font-weight: bold;
    box-sizing: border-box;
}
.buttonBox {
    width: 100%;
    height: calc(100% - 210px);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: 10px;
}

button {
    border: none;
    background-color: #8f8c9d;
    font-size: 24px;
    color: rgb(255, 255, 255);
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    outline: none;
}


.casio {
    text-align: center;
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    color: rgb(255, 255, 255);
    padding-bottom: 1rem;
}
.casio .upper{
    font-size: 80%;
}

.casio h3 {
    padding-bottom: .5rem;
}

button:hover {
    background-color: #5f5d68;
}

.plus, 
.equals, 
.minus, 
.decimal, 
.multiply, 
.divide, 
.percent, 
.plusminus, 
.reset {
    background-color: #1e1d24;
}

.plus:hover, 
.equals:hover, 
.minus:hover, 
.decimal:hover, 
.multiply:hover, 
.slash:hover, 
.percent:hover, 
.plusminus:hover, 
.reset:hover {
    background-color: rgb(20, 16, 39)
}

.equals {
    grid-column: 3 / 5;
}
.casio *{
  font-family: 'Oswald', sans-serif;
  letter-spacing: 0.1em;


}

* {
  margin: 0;
  padding: 0;
  font-family: "roboto", sans-serif;
}


body {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
